.switch {
    position: relative;
    height: 32px;
    width: 52px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(surfaceVariant);
    transition: .4s;
    border-radius: 34px;
}

/*input*/
.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 3px;
    bottom: 4px;
    background-color: var(--outline);
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: var(--primary);
}

input + .slider {
    border: solid 2px var(--outline);
}

input:checked + .slider:before {
    transform: translateX(20px);
    bottom: 1px;
    left: 0px;
    background-color: var(--onPrimary);
    height: 26px;
    width: 26px;
}